* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.main_wrap {
    width: 100%;
    height: 100vh;
    display: flex;
}
.info_wrap {
    width: 50%;
    height: 100vh;
    display: grid;
    place-items: center;
}

.info_card {
    width: 90%;
    max-width: 500px;
    border: 2px solid #000;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.info_input , .info_radio_wrap {
    width: 90%;
    padding: 8px 10px;
    display: block;
    margin: auto;
    border: 2px solid #000;
    border-radius: 7px;
    margin-bottom: 10px;
    font-size: 20px;
}

.info_input:focus {
    outline: none;
}

.info_radio_wrap {
    border: none;
    display: flex;
    justify-content: space-between;
}

.info_btn {
    width: 90%;
    display: block;
    margin: auto;
    padding: 8px 10px;
    font-size: 20px;
}

.card_wrap {
    width: 50%;
    padding: 20px;
    font-size: 20px;
}

.card {
    padding: 10px;
    border: 2px solid #000;
    border-radius: 7px;
    margin-bottom: 20px;
}

.vtn {
    font-size: 18px;
    padding: 5px 7px;
    margin: 0px 5px;
}